import React, { PropTypes } from 'react';
import { NODE_CONTENT_GROUP } from '../utils/const';


const NodeContent = ({ groups, selectQueryHandler, locale }) => {

  return (
        <div>
        { groups.map( (group, i) => {
          var group_content = '';

          const onClick = () => {
            selectQueryHandler(group.Id); 
          };

          switch (group.Type)
          {
            case NODE_CONTENT_GROUP:
              
              group_content = 
              <div className="col-4 indicator_group_thumb" key={group.Id}>
              <span className="indicator_group_thumb"/>
              <div  className="indicator_group_thumb indicator_group"  onClick={ onClick } >{locale == 'fr' ? group.FrnLabel : group.EngLabel}
              <span className ="indicator_queries_number">[{group.Children.length}]</span>
              </div></div>;

              break;
            default:
              //nothing
              break;
          }
          return group_content;
        }
        )}
        </div>
      );
};

NodeContent.propTypes = {
  groups: PropTypes.array.isRequired,
  selectQueryHandler: PropTypes.func,
  locale:             PropTypes.string
};

export default NodeContent;



