import fetchJsonp from 'fetch-jsonp'
import _ from 'lodash';

//Temprary data API class, untill the real one is available
//////////////////////////////////////////////////////////////////
export const INICATOR_GROUP_DATA_API_REQUEST   = 'http://das.uis.unesco.org/api/contenttree/?treeid=0&sector=';


export  class FetchDataAPI  {

constructor() {
  }

//sector      - sector for which indicator groups are retrieved
//dispatch    - redux displatch method
//receiveData - redux handler, when data received, its first shoulld be converted and then passed to this method
/////////////////////////////////////////////////////////////////////////////////////
 static fetchData(sector, dispatch, receiveData) {

    return fetchJsonp(INICATOR_GROUP_DATA_API_REQUEST+sector)
                .then(response => response.json())
                .then(json => 
                {
                  //Here convert data to the internal format
                  //TODO: will have to rework it once we have real Data API
                  //At the end we call receiveData method 
                  /////////////////////////////////////////////////////////
                  dispatch(receiveData(sector, json.Root.Children[0].Children[0].Children))

                });

  }

}


