import React, { Component, PropTypes } from 'react';
import { render } from 'react-dom'
import { Provider } from 'react-redux'
import IndicatorGroup from './containers/indicator-group'
import configureStore from './store/configureStore'
//import 'font-awesome-webpack';
import './assets/theme-default.less';
import './assets/fluidable.less';

//Standalone usage uncomment this line
//then you can pass this object to IndicatorGroup Component instead of the property
import { FetchDataAPI }  from './utils/fetch-data-api'

class App extends Component {
  constructor({props}) {
    super(props);
    this.store = configureStore();
    
  }


  render() {

    return (
      <Provider store={ this.store }>
        <IndicatorGroup fetchDataAPI={ this.props.fetchDataAPI }  />
      </Provider>
      );
  }
};


App.PropTypes = {
  fetchDataAPI: PropTypes.func
};


export default App;

render(<App />, document.getElementById('root'));


