import React, { Component, PropTypes } from 'react'
import {IntlProvider, FormattedMessage, addLocaleData, injectIntl, intlShape} from 'react-intl';
import { EDUCATION,  SCIENCE, DEMOGRAPHIC, CULTURE, COMMUNICATION} from '../utils/const';


//this is temporary control will be removed in future, just to provide a way to select sector for now.
const Picker = ({value, onChange, intl }) => {


   var options = new Map([
                     [EDUCATION,     intl.formatMessage({id: EDUCATION})], 
                     [SCIENCE,       intl.formatMessage({id: SCIENCE})], 
                     [DEMOGRAPHIC,   intl.formatMessage({id: DEMOGRAPHIC})], 
                     [CULTURE,       intl.formatMessage({id: CULTURE})], 
                     [COMMUNICATION, intl.formatMessage({id: COMMUNICATION})] ]);

    return (
      <span>

        <h1>{ options.get(value) }</h1>

        <select onChange={e => onChange(e.target.value)}
                value={value}>
          {
            Array.from( options ).map(option =>
            <option value={option[0]} key={option[0]}>
            {option[1]}
            </option>)
          }
        </select>
      </span>
    )
}

Picker.propTypes = {
  value: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  intl   : intlShape.isRequired
}

export default injectIntl(Picker);




