
//Actions applicable to all the platforms
//////////////////////////////////////////////////
export const REQUEST_DATA    = 'REQUEST_DATA'
export const RECEIVE_DATA    = 'RECEIVE_DATA'
export const SELECT_SECTOR   = 'SELECT_SECTOR'
export const INVALIDATE_DATA = 'INVALIDATE_DATA'
export const SELECT_QUERY    = 'SELECT_QUERY'
export const SELECT_LOCALE   = 'SELECT_LOCALE'

//Mobile only actions
//////////////////////////////////////////////////
export const MB_SELECT_GROUP = 'MB_SELECT_GROUP'
export const MB_GO_BACK      = 'MB_GO_BACK'
import { DEFAULT_SECTOR } from '../utils/const';

export function selectLocale(locale) {
  return { type: SELECT_LOCALE, locale };
}

export function selectSector(sector) {
  return { type: SELECT_SECTOR, sector };
}

export const selectQuery = (sector, queryId) => {
  return { type: SELECT_QUERY, sector, queryId };
}

export const selectGroup = (sector, groupId) => {
  return { type: MB_SELECT_GROUP, sector, groupId };
}

export const goBack = ( sector ) => {
  return { type: MB_GO_BACK, sector };
}

export function invalidateSectorData(sector) {
  return { type: INVALIDATE_DATA, sector };
}

function requestData(sector) {
  return { type: REQUEST_DATA, sector };
}

function receiveData(sector, json) {
  return {
    type: RECEIVE_DATA,
    sector: sector,
    groups: json,
    receivedAt: Date.now()
  };
}

function fetchData(sector, fetchDataAPI) {

  return dispatch => {
    dispatch(requestData(sector));
    if(fetchDataAPI  === undefined)
      return undefined;
    
    return fetchDataAPI.fetchData(sector, dispatch, receiveData);
  }

}

function shouldFetchData(state, sector) {

  const data = state.sectorData[sector];
  if (!data) {
    return true
  }
  if (data.isFetching) {
    return false
  }
  return data.didInvalidate;
}

export function fetchSectorDataIfNeeded(sector, fetchDataAPI) {

  return (dispatch, getState) => {
    if (shouldFetchData(getState(), sector)) {
      return dispatch(fetchData(sector, fetchDataAPI))
    }

  }
}



