import { combineReducers } from 'redux'
import { SELECT_SECTOR, INVALIDATE_DATA, REQUEST_DATA, RECEIVE_DATA, SELECT_LOCALE, SELECT_QUERY, MB_SELECT_GROUP, MB_GO_BACK } from '../actions'
import { DEFAULT_SECTOR } from '../utils/const';

function selectedSector(state = DEFAULT_SECTOR, action) {
  
  switch (action.type) 
  {
    case SELECT_SECTOR:
      return action.sector
    default:
      return state
  }
}

function selectedLocale(state = 'en', action) {
  
  switch (action.type) 
  {
    case SELECT_LOCALE:
      return action.locale
    default:
      return state
  }
}

function groups(state = { isFetching: false, didInvalidate: false, items: []}, action) {

  switch (action.type) 
  {
    case INVALIDATE_DATA:
      return Object.assign({}, state, {
        didInvalidate: true,
        queryId: undefined,
        groupId: undefined
      })
    case REQUEST_DATA:
      return Object.assign({}, state, {
        isFetching: true,
        didInvalidate: false,
        queryId: undefined,
        groupId: undefined
      })
    case RECEIVE_DATA:
      return Object.assign({}, state, {
        isFetching: false,
        didInvalidate: false,
        items: action.groups,
        lastUpdated: action.receivedAt,
        queryId: undefined,
        groupId: undefined
      })
    case SELECT_QUERY:
    return Object.assign({}, state, {
        queryId: action.queryId
      })
    case MB_SELECT_GROUP:
    return Object.assign({}, state, {
        groupId: action.groupId,
        queryId: undefined
      })
    case MB_GO_BACK:
      return Object.assign({}, state, {
        queryId: undefined,
        groupId: undefined
      })
    default:
      return state
  }
}

function sectorData(state = { }, action) {

  switch (action.type) 
  {
    case INVALIDATE_DATA:
    case RECEIVE_DATA:
    case REQUEST_DATA:
    case SELECT_QUERY:
    case MB_SELECT_GROUP:
    case MB_GO_BACK:
      return Object.assign({}, state, {
        [action.sector]: groups(state[action.sector], action)
      })
    default:
      return state
  }
}

const rootReducer = combineReducers({
  sectorData,
  selectedSector,
  selectedLocale
})

export default rootReducer



