import React, { Component, PropTypes  } from 'react';
import _ from 'lodash';
import NodeContent from './node-content';
import {IntlProvider, FormattedMessage, injectIntl, intlShape} from 'react-intl';
import MediaQuery from 'react-responsive';

const Node = ({nodes, selectQueryHandler, selectGroupHandler, goBackHandler, locale, groupId}) => {

    const renderNodeWithContent = ( node, selectQueryHandler, locale ) => {
      return (<div className="col-4" key={node.Id}>
              <div className="col-group indicator_group_title">{locale == 'fr' ? node.FrnLabel : node.EngLabel}</div>
              <NodeContent groups={node.Children} selectQueryHandler={selectQueryHandler} locale={locale}  /> 
              <div className="col-8"><br/></div></div>);
    }

    const renderSingleNode = ( node, onGroupClick, locale ) => {
      return (<div className="col-4" key={node.Id}>
              <div className="col-group indicator_group_title_thumb" onClick={ onGroupClick } >{locale == 'fr' ? node.FrnLabel : node.EngLabel }</div>
              <div className="col-8"><br/></div></div>);
    }

    const onGoBack = () => {
            goBackHandler(); 
        };


    return (
      <div>

        <MediaQuery maxWidth={1224}>
        <div>
        {  //This is Mobile version, render BACK button
           //////////////////////////////////////////////////////////////
            groupId !== undefined &&
            <div>
            <a  href="#"
               onClick={onGoBack}>
              BACK
            </a><br/></div>
        }
        { 
         nodes.map((node) => {

          //This is Mobile version
          //////////////////////////////////////////////////////////////
          const onGroupClick = () => {
            selectGroupHandler(node.Id); 
          };


          if(groupId !== undefined)
          {
            //if groupId is selected then render only the selected group
            //////////////////////////////////////////////////////////////
            if(node.Id === groupId)
            {
              return renderNodeWithContent(node, selectQueryHandler, locale);
            }
          }
          else
          {
            switch (node.Type)
            {
              case 'Group':
                    return renderSingleNode(node, onGroupClick, locale);
              break;
              default:
               //nothing
              break;
            }
          }

          }) 
        }
        </div>
        </MediaQuery>

        <MediaQuery minWidth={1224}>
        <div>
        {
          //This is Desktop version
          //////////////////////////////////////////////////////////////
          nodes.map((node) => {

            switch (node.Type)
            {
              case 'Group':
                   return renderNodeWithContent(node, selectQueryHandler, locale);
              break;
              default:
               //nothing
              break;
          }

          }) //end return
        }
        </div>
        </MediaQuery>
      
      </div>
      );   
  

 }


Node.propTypes = {
  nodes:              PropTypes.array.isRequired,
  selectQueryHandler: PropTypes.func,
  selectGroupHandler: PropTypes.func, 
  goBackHandler:      PropTypes.func, 
  locale:             PropTypes.string,
  groupId:            PropTypes.number
}

//export default injectIntl(Node);
export default Node;


