import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { selectSector, fetchSectorDataIfNeeded, invalidateSectorData, selectQuery, selectGroup, selectLocale, goBack } from '../actions'
import Picker from '../views/picker'
import Node from '../views/node'
import {IntlProvider, FormattedMessage, addLocaleData, injectIntl, intlShape} from 'react-intl';
import messages from '../messages' 
import en from 'react-intl/locale-data/en';
import fr from 'react-intl/locale-data/fr';

addLocaleData(en);
addLocaleData(fr);

class IndicatorGroup extends Component {

  constructor(props) {

    super(props)

    this.handleChange        = this.handleChange.bind(this)
    this.handleRefreshClick  = this.handleRefreshClick.bind(this)
    this.handlQuerySelect    = this.handlQuerySelect.bind(this)
    this.handleLanguageClick = this.handleLanguageClick.bind(this)
    this.handlGroupSelect    = this.handlGroupSelect.bind(this)
    this.handleGoBack        = this.handleGoBack.bind(this)
  }

  componentDidMount() {

    const { dispatch, selectedSector, fetchDataAPI } = this.props
    dispatch( fetchSectorDataIfNeeded(selectedSector, fetchDataAPI) )

  }

  componentWillReceiveProps(nextProps) {

    if (nextProps.selectedSector !== this.props.selectedSector) {

      const { dispatch, selectedSector, fetchDataAPI } = nextProps
      dispatch( fetchSectorDataIfNeeded(selectedSector, fetchDataAPI))

    }

  }

  handleChange(nextSector) {
    this.props.dispatch(selectSector(nextSector))
  }

  handleRefreshClick(e) {

    e.preventDefault()

    const { dispatch, selectedSector, fetchDataAPI } = this.props

    //Invalidate 
    dispatch(invalidateSectorData(selectedSector))
    dispatch(fetchSectorDataIfNeeded(selectedSector, fetchDataAPI))
  }

  handlQuerySelect(queryid){

    const { dispatch, selectedSector, selectQuery } = this.props
    dispatch(selectQuery(selectedSector, queryid));
  }

  handlGroupSelect(groupid){

    const { dispatch, selectedSector, selectGroup } = this.props
    dispatch(selectGroup(selectedSector, groupid));
  }

  handleLanguageClick(language) {

    const { dispatch } = this.props
    dispatch(selectLocale(language))
  }

  handleGoBack(){

    const { dispatch, selectedSector } = this.props
    dispatch(goBack(selectedSector))
  }

  render() {

    const { selectedSector, nodes, isFetching, lastUpdated, selectedLocale, groupId } = this.props
    const isEmpty = nodes.length === 0
    
    //TODO: Refactor options code EN/FR etc..
    return (
      <IntlProvider locale={selectedLocale} messages={messages[selectedLocale]}>
        <div>
        

        <a  href='#' onClick={this.handleLanguageClick.bind(this, 'en')}>EN</a>
        &nbsp;&nbsp;
        <a  href='#' onClick={this.handleLanguageClick.bind(this, 'fr')}>FR</a>

        <Picker value={selectedSector}
                onChange={this.handleChange} /> 
        <p>
          {lastUpdated &&
            <span>
              Last updated at {new Date(lastUpdated).toLocaleTimeString()}.
              {' '}
            </span>
          }
          {!isFetching &&
            <a href="#"
               onClick={this.handleRefreshClick}>
              Refresh
            </a>
          }
        </p>
        {isEmpty
          ? (isFetching ? <h2>Loading...</h2> : <h2>Empty.</h2>)
          : <div style={{ opacity: isFetching ? 0.5 : 1 }}>
              <Node nodes              = {nodes} 
                    selectQueryHandler = {this.handlQuerySelect} 
                    selectGroupHandler = {this.handlGroupSelect} 
                    goBackHandler      = {this.handleGoBack}
                    locale             = {selectedLocale} 
                    groupId            = {groupId} />
            </div>
        }
        </div>
      </IntlProvider>
    )
  }

}

IndicatorGroup.propTypes = {
  selectedSector: PropTypes.string.isRequired,
  nodes:          PropTypes.array.isRequired,
  isFetching:     PropTypes.bool.isRequired,
  lastUpdated:    PropTypes.number,
  dispatch:       PropTypes.func.isRequired,
  selectQuery:    PropTypes.func.isRequired,
  selectGroup:    PropTypes.func.isRequired,
  goBack:         PropTypes.func.isRequired,
  selectedLocale: PropTypes.string,
  groupId:        PropTypes.number,
  queryId:        PropTypes.number,
  fetchDataAPI:   PropTypes.func
}

function mapStateToProps(state) {

  const { selectedSector, sectorData, selectedLocale } = state

  const {
    isFetching,
    lastUpdated,
    items: nodes,
    groupId
  } = sectorData[selectedSector] || {
    isFetching: true,
    items: [],
    groupId: undefined,
    queryId: undefined
  }

  return {
    selectedSector,
    nodes,
    isFetching,
    lastUpdated,
    selectQuery,
    selectGroup,
    goBack,
    selectedLocale,
    groupId
  }
}

export default connect(mapStateToProps)(IndicatorGroup)






